import React from 'react'
import { Link } from 'react-router-dom'

function ProductList({ data }) {
    return (
        <li style={{ border: '1px solid #EDEDF4', borderRadius: 5, padding: 7 }}>
            <div className="shop_layout_1">
                <div className="shop_image_wrap relative">
                    <div className="shop_img_prod">
                        <Link to={`/product/${data.slug}`} >
                            <img xoriginal={data.image_url} data-zoom={data.image_url} src={data.image_url} loading="lazy" alt={`${data.title} BY ${data.brand_name} - ${data.category_name} available at DOYUF`} />
                        </Link>
                    </div>
                    <div style={{ position: 'absolute', top: 0, left: 0 }}>
                    </div>
                </div>
                <div className="shop_content">
                    <h3 className="shop_title">{data.brand_name}</h3>
                    <Link to={`/product/${data.slug}`}>
                        <h4 className="shop_desc">{data.title}</h4>
                    </Link>
                    <div className="shop_price d-block">
                        <span className="sale_price text-danger">AED {data.sale_price} </span>
                        <del className="del-price">AED {data.price}</del>
                        <span className="sale_price text-danger">20% Off</span>
                    </div>
                    <h4 className="shop_desc" style={{ color: 'red', fontWeight: 500 }}><i className="fa fa-hourglass-half" /> Only 1 left!</h4>
                    <div className="d-flex justify-content-between">
                        <span style={{ fontSize: 13, padding: '0px 8px', background: '#c89858', color: '#fff', borderRadius: '8.5px', textAlign: 'center', display: 'flex', justifyContent: 'center', alignItems: 'center', height: 23 }}>⭐
                            3.6</span>
                    </div>
                </div>
                <div className="prod-hover" style={{ bottom: '-2%', borderRadius: '0px 0px 0 0', padding: '25px 18px' }}>
                    <a href="#" className="btn btn_primary d-block add_to_cart add_to_cart_ajax_new " data-bs-toggle="tooltip" data-bs-placement="left" title data-attributes data-id={192056} data-bs-original-title="ADD TO BAG" aria-label="ADD TO BAG">ADD TO BAG</a>
                    <Link to={`/product/${data.slug}`} className="d-block view-details">VIEW DETAILS</Link>
                </div>
            </div>
        </li>

    )
}

export default ProductList