import React, { useEffect, useRef, useState } from 'react'
import Carousel from '../components/Carousel'
import ProductList from '../components/ProductList'
import { LazyLoadImage } from 'react-lazy-load-image-component'

function Homepage() {
  const [HomeOne, setHomeOne] = useState([]);
  const [HomeTwo, setHomeTwo] = useState([]);
  const [HomeFour, setHomeFour] = useState([]);
  const [HomeFive, setHomeFive] = useState([]);
  const [HomeSix, setHomeSix] = useState([]);
  const [Exclusive, setExclusive] = useState([]);
  const [Sunglasses, setSunglasses] = useState([]);
  const [Bags, setBags] = useState([]);

  const cachedData = useRef({});

  useEffect(() => {
    fetchDataIfNotCached(1, setHomeOne);
    fetchDataIfNotCached(2, setHomeTwo);
    fetchDataIfNotCached(4, setHomeFour);
    fetchDataIfNotCached(5, setHomeFive);
    fetchDataIfNotCached(6, setHomeSix);
    fetchDataIfNotCached(0, setExclusive, 'homepage exclusive');
    fetchDataIfNotCached(0, setSunglasses, 'homepage sunglasses');
    fetchDataIfNotCached(0, setBags, 'homepage bags');
  }, []);

  const fetchDataIfNotCached = (type, setState, badge) => {
    if (cachedData.current[type] && (!badge || cachedData.current[badge])) {
      setState(cachedData.current[type]);
    } else {
      GetHomepageProducts(type, (data) => {
        cachedData.current[type] = data;
        if (badge) {
          cachedData.current[badge] = data;
        }
        setState(data);
      }, badge);
    }
  };

  const GetHomepageProducts = (type, callback, badge) => {
    const requestOptions = {
      method: "GET",
      redirect: "follow"
    };

    fetch(`https://www.doyuf.com/getHomepageProducts?type=${type}&badge=${badge ?? ''}`, requestOptions)
      .then((response) => response.json())
      .then((result) => {
        if (result.status === 200) {
          callback(result.all_products);
        }
      })
      .catch((error) => console.error(error));
  };
  return (
    <>

      <main class="main-section">
        <div class="main_header_categories" style={{ margin: 0 }}>
          <div class="container">
            <div class=" header_categories_col custom-scroll">
              <div className="header_categories">
                <div className="single_category">
                  <a>
                    <img data-src="https://d3c5vsbepl6np2.cloudfront.net/images/bestseller.jpg" src="{{ asset('assets/images/mobile-icons/drawer/bestseller.jpg') }}" alt="Ramadan Sale" width="70px" height="70px" className="Image lazyload" />
                  </a>
                  <span>Best Seller</span>
                </div>
                <div className="single_category">
                  <a>
                    <img data-src="https://d3c5vsbepl6np2.cloudfront.net/images/Frame 63.jpg" src="{{ asset('assets/images/new_home_images/5/men women icon/Frame 63_lqip.jpg') }}" alt="Shoes" width="70px" height="70px" className="Image lazyload" />
                  </a>
                  <span>Women</span>
                </div>
                <div className="single_category">
                  <a>
                    <img data-src="https://d3c5vsbepl6np2.cloudfront.net/images/Frame 62.jpg" src="{{ asset('assets/images/new_home_images/5/men women icon/Frame 62_lqip.jpg') }}" alt="Shoes" width="70px" height="70px" className="Image lazyload" />
                  </a>
                  <span>Men</span>
                </div>
                <div className="single_category">
                  <a>
                    <img data-src="https://d3c5vsbepl6np2.cloudfront.net/images/kids.jpg" src="{{ asset('assets/images/new_home_images/5/men women icon/kids_lqip.jpg') }}" alt="Kids" width="70px" height="70px" className="Image lazyload" />
                  </a>
                  <span>KIDS</span>
                </div>
                <div className="single_category">
                  <a>
                    <img data-src="https://d3c5vsbepl6np2.cloudfront.net/images/Group 155.jpg" src="{{ asset('assets/images/new_home_images/5/men women icon/Group 155_lqip.jpg') }}" alt="Shoes" width="70px" height="70px" className="Image lazyload" />
                  </a>
                  <span>Bags</span>
                </div>
                <div className="single_category">
                  <a>
                    <img data-src="https://d3c5vsbepl6np2.cloudfront.net/images/Group 157.jpg" src="{{ asset('assets/images/new_home_images/5/men women icon/Group 157_lqip.jpg') }}" alt="Shoes" width="70px" height="70px" className="Image lazyload" />
                  </a>
                  <span>Sunglasses</span>
                </div>
                <div className="single_category">
                  <a>
                    <img data-src="https://d3c5vsbepl6np2.cloudfront.net/images/Group 156.jpg" src="{{ asset('assets/images/new_home_images/5/men women icon/Group 156_lqip.jpg') }}" alt="Shoes" width="70px" height="70px" className="Image lazyload" />
                  </a>
                  <span>Shoes</span>
                </div>
                <div className="single_category">
                  <a>
                    <img data-src="https://d3c5vsbepl6np2.cloudfront.net/images/jewelry.webp" src="{{ asset('assets/images/mobile-icons/drawer/jewelry.webp') }}" alt="Jewelry" width="70px" height="70px" className="Image lazyload" />
                  </a>
                  <span>Jewellery</span>
                </div>
              </div>

            </div>
          </div>
        </div>
      </main>
      <Carousel />
      <div class="mt-4 homepagetodayweb translate">
        <h2 class="stylishHeading">Today's Deals</h2>
      </div>
      <div className='mt-4 ProductHomeGridScroll'>
        {HomeOne && HomeOne.map((el, index) => (
          <ProductList data={el} key={index} />
        ))}
      </div>
      <div class="mt-4 homepagebestweb translate">
        <h2 class="stylishHeading">Best Seller</h2>
      </div>
      <div className='mt-4 ProductHomeGridScroll'>
        {HomeTwo && HomeTwo.map((el, index) => (
          <ProductList data={el} key={index} />
        ))}
      </div>
      <div class="mt-4 homepagetrendingweb translate">
        <h2 class="stylishHeading">Trending</h2>
      </div>
      <div className='mt-4 ProductHomeGridScroll'>
        {HomeFour && HomeFour.map((el, index) => (
          <ProductList data={el} key={index} />
        ))}
      </div>
      <div className='mt-4 ProductHomeGrid'>
        {HomeFive && HomeFive.map((el, index) => (
          <ProductList data={el} key={index} />
        ))}
        {HomeSix && HomeSix.map((el, index) => (
          <ProductList data={el} key={index} />
        ))}
      </div>
      <div className="Exclusive">
        <LazyLoadImage src="https://www.doyuf.com/assets/images/banner/category/exclusivenew.webp" style={{ width: '100%' }} />
      </div>
      <div className='mt-4 ProductHomeGrid'>
        {Exclusive && Exclusive.map((el, index) => (
          <ProductList data={el} key={index} />
        ))}
      </div>
      <div className="Sunglasses">
        <LazyLoadImage src="https://www.doyuf.com/assets/images/banner/category/watches_new.webp" style={{ width: '100%' }} />
      </div>
      <div className='mt-4 ProductHomeGrid'>
        {Sunglasses && Sunglasses.map((el, index) => (
          <ProductList data={el} key={index} />
        ))}
      </div>
      <div className="Bags">
        <LazyLoadImage src="https://www.doyuf.com/assets/images/banner/category/bagsnew.webp" style={{ width: '100%' }} />
      </div>
      <div className='mt-4 ProductHomeGrid'>
        {Bags && Bags.map((el, index) => (
          <ProductList data={el} key={index} />
        ))}
      </div>
    </>
  )
}

export default Homepage