import React, { useEffect, useState } from 'react'
import ProductList from '../components/ProductList';
import ScrollToTop from '../components/ScrollToTop';

function ListingPage() {
    const [Data, setData] = useState([])
    const [Products, setProducts] = useState([])
    const [Page, setPage] = useState(0)
    const [IsLoading, setIsLoading] = useState(false)

    useEffect(() => {

        const requestOptions = {
            method: "GET",
            redirect: "follow"
        };
        const queryParams = new URLSearchParams(window.location.search);

        fetch(`https://www.doyuf.com/getProducts?${queryParams.toString()}`, requestOptions)
            .then((response) => response.json())
            .then((result) => {
                // if (result.status === 200) {
                setData(result);
                setProducts(result.all_products)
                // }
            })
            .catch((error) => console.error(error));
    }, [])
    const LoadMoreProducts = (page) => {
        setIsLoading(true);
        const requestOptions = {
            method: "GET",
            redirect: "follow"
        };
        const queryParams = new URLSearchParams(window.location.search);

        fetch(`https://www.doyuf.com/getProducts?${queryParams.toString()}&page=${page}`, requestOptions)
            .then((response) => response.json())
            .then((result) => {
                setIsLoading(false);
                setPage(page)
                setData(result);
                setProducts(prevProducts => [...prevProducts, ...result.all_products]);
            })
            .catch((error) => console.error(error));
    }

    return (
        <div>
            <ScrollToTop />
            {Data.all_products ?
                <>
                    <div className='ProductHomeGrid MarginTop200' >
                        {Products.map((el, index) => (
                            <ProductList data={el} key={index} />
                        ))}
                    </div>
                    {Data.total_count > 30 ?
                        IsLoading == false ?
                            <div style={{ justifyContent: 'center', alignItems: 'center', display: 'flex' }} className="LoadProductsButton my-3">
                                <button className="btn btn_primary text-center load_more_products_web" style={{ padding: '10px 131px', display: 'flex', justifyContent: 'center', alignItems: 'center' }} onMouseEnter={() => LoadMoreProducts(Page + 1)}>Load More &nbsp;<span id="CountProducts"> {Products.length} / {Data.total_count} </span></button>
                            </div> : <div className="text-center loader-center"><img src="https://www.doyuf.com/assets/images/loader.gif" /></div>
                        : ''
                    }
                </>
                :
                <div className="row mt-5" id="Skeleton">
                    <div className="new_desktop_view" >
                        <ul style={{ display: 'grid', gridTemplateColumns: 'repeat(5,1fr)', gridGap: '19.7px 28.6px', marginTop: '120px' }}>
                            <li className="skeleton" style={{ border: '1px solid #EDEDF4', borderRadius: 5, padding: 7, height: 330 }}>
                                <div className="shop_layout_1 ">
                                    <div className="shop_image_wrap relative">
                                        <div className="shop_img_prod">
                                        </div>
                                    </div>
                                    <div className="shop_content">
                                    </div>
                                </div>
                            </li>
                            <li className="skeleton" style={{ border: '1px solid #EDEDF4', borderRadius: 5, padding: 7, height: 330 }}>
                                <div className="shop_layout_1 ">
                                    <div className="shop_image_wrap relative">
                                        <div className="shop_img_prod">
                                        </div>
                                    </div>
                                    <div className="shop_content">
                                    </div>
                                </div>
                            </li>
                            <li className="skeleton" style={{ border: '1px solid #EDEDF4', borderRadius: 5, padding: 7, height: 330 }}>
                                <div className="shop_layout_1 ">
                                    <div className="shop_image_wrap relative">
                                        <div className="shop_img_prod">
                                        </div>
                                    </div>
                                    <div className="shop_content">
                                    </div>
                                </div>
                            </li>
                            <li className="skeleton" style={{ border: '1px solid #EDEDF4', borderRadius: 5, padding: 7, height: 330 }}>
                                <div className="shop_layout_1 ">
                                    <div className="shop_image_wrap relative">
                                        <div className="shop_img_prod">
                                        </div>
                                    </div>
                                    <div className="shop_content">
                                    </div>
                                </div>
                            </li>
                            <li className="skeleton" style={{ border: '1px solid #EDEDF4', borderRadius: 5, padding: 7, height: 330 }}>
                                <div className="shop_layout_1 ">
                                    <div className="shop_image_wrap relative">
                                        <div className="shop_img_prod">
                                        </div>
                                    </div>
                                    <div className="shop_content">
                                    </div>
                                </div>
                            </li>
                            <li className="skeleton" style={{ border: '1px solid #EDEDF4', borderRadius: 5, padding: 7, height: 330 }}>
                                <div className="shop_layout_1 ">
                                    <div className="shop_image_wrap relative">
                                        <div className="shop_img_prod">
                                        </div>
                                    </div>
                                    <div className="shop_content">
                                    </div>
                                </div>
                            </li>
                            <li className="skeleton" style={{ border: '1px solid #EDEDF4', borderRadius: 5, padding: 7, height: 330 }}>
                                <div className="shop_layout_1 ">
                                    <div className="shop_image_wrap relative">
                                        <div className="shop_img_prod">
                                        </div>
                                    </div>
                                    <div className="shop_content">
                                    </div>
                                </div>
                            </li>
                            <li className="skeleton" style={{ border: '1px solid #EDEDF4', borderRadius: 5, padding: 7, height: 330 }}>
                                <div className="shop_layout_1 ">
                                    <div className="shop_image_wrap relative">
                                        <div className="shop_img_prod">
                                        </div>
                                    </div>
                                    <div className="shop_content">
                                    </div>
                                </div>
                            </li>
                            <li className="skeleton" style={{ border: '1px solid #EDEDF4', borderRadius: 5, padding: 7, height: 330 }}>
                                <div className="shop_layout_1 ">
                                    <div className="shop_image_wrap relative">
                                        <div className="shop_img_prod">
                                        </div>
                                    </div>
                                    <div className="shop_content">
                                    </div>
                                </div>
                            </li>
                            <li className="skeleton" style={{ border: '1px solid #EDEDF4', borderRadius: 5, padding: 7, height: 330 }}>
                                <div className="shop_layout_1 ">
                                    <div className="shop_image_wrap relative">
                                        <div className="shop_img_prod">
                                        </div>
                                    </div>
                                    <div className="shop_content">
                                    </div>
                                </div>
                            </li>
                        </ul>
                    </div>
                    <div className="new_mobile_view">
                        <ul className="row" style={{
                            margin: '0px',
                            padding: '0px'
                        }}>
                            <li className="col-6 skeleton" style={{ border: '1px solid #EDEDF4', borderRadius: 5, padding: 7, height: 330 }}>
                                <div className="shop_layout_1 ">
                                    <div className="shop_image_wrap relative">
                                        <div className="shop_img_prod">
                                        </div>
                                    </div>
                                    <div className="shop_content">
                                    </div>
                                </div>
                            </li>
                            <li className="col-6 skeleton" style={{ border: '1px solid #EDEDF4', borderRadius: 5, padding: 7, height: 330 }}>
                                <div className="shop_layout_1 ">
                                    <div className="shop_image_wrap relative">
                                        <div className="shop_img_prod">
                                        </div>
                                    </div>
                                    <div className="shop_content">
                                    </div>
                                </div>
                            </li>
                            <li className="col-6 skeleton" style={{ border: '1px solid #EDEDF4', borderRadius: 5, padding: 7, height: 330 }}>
                                <div className="shop_layout_1 ">
                                    <div className="shop_image_wrap relative">
                                        <div className="shop_img_prod">
                                        </div>
                                    </div>
                                    <div className="shop_content">
                                    </div>
                                </div>
                            </li>
                            <li className="col-6 skeleton" style={{ border: '1px solid #EDEDF4', borderRadius: 5, padding: 7, height: 330 }}>
                                <div className="shop_layout_1 ">
                                    <div className="shop_image_wrap relative">
                                        <div className="shop_img_prod">
                                        </div>
                                    </div>
                                    <div className="shop_content">
                                    </div>
                                </div>
                            </li>
                        </ul>
                    </div>
                </div>
            }
        </div >
    )
}

export default ListingPage